import React, { useState, useRef } from "react";

function VideoPlayer() {
  const [currentVideo, setCurrentVideo] = useState(0);
  const sources = [
    "http://docs.evostream.com/sample_content/assets/bun33s.mp4",
    "http://docs.evostream.com/sample_content/assets/bunny.mp4",
    "http://docs.evostream.com/sample_content/assets/poppres240p.mp4"
  ];
  const videoRef = useRef(null);

  const handlePause = (event) => {
    console.log("Video paused!");
  };

  const handleEnd = (event) => {
    // Handle the end event
    console.log("Video ended!");
    // Play the next video
    console.log("Playing the next video...");
    setCurrentVideo(currentVideo + 1);
    document.title = "Video: " + currentVideo;
  };
  console.log(currentVideo);
  return (
    <div>
      <video
        autoPlay
        ref={videoRef}
        onPause={handlePause}
        onEnded={handleEnd}
        controls
        src={sources[currentVideo]}
        type="video/mp4"
      >
        Your browser does not support the video tag.
      </video>
    </div>
  );
}

export default VideoPlayer;
